.singlePost{
    flex:9;
    
}

.singlePostWrapper{
    padding: 20px;
    padding-right: 0;

}

.singlePostImg{
    width: 100%;
    height: 300px;
    border-radius: 5px;
    object-fit: cover;
}

.singlePostTitle{
    text-align: center;
    margin: 10px;
    font-size: 30px;
}

.singlePostEdit{
    float: right;
    font-size: 16px;


}

.singlePostIcon{
   margin-left: 10px;
   cursor: pointer;
}

.singlePostIcon:first-child{
    color: aqua;
}

.singlePostIcon:last-child{
    color: yellowgreen;
}

.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: blueviolet;
}

.singlePostDescription{
    color: #666;
    font-size: 20px;
    line-height: 25px;
}

.singlePostDescription::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600%;
}

