* {
    box-sizing: border-box;
  }
  
  body {
    padding: 20px;
    background-color: #F9F9F9;
  }
  
  /* Header/Blog Title */
  .header {
    padding: 30px;
    font-size: 40px;
    text-align: center;
    background-color: #346299;
  }
  
 
  .leftcolumn {
    float: left;
    width: 75%;
  }



  .ImgContent{
    background-color: #CCC9E7;
    width: 50%;
    padding: 10px;
    object-fit:cover;
  }
  
  .walkvid{
    margin-top: 30px;
    width: 40%;
    height: 20vw;
    position: relative;

   }

  /* Right column */
  .rightcolumn {
    float: left;
    width: 25%;
    padding-left: 20px;
  }
  
  /* Fake image */
  .rightcolumn img {
    background-color: #aaa;
    width: 100%;
    padding: 5px;
    object-fit:cover;
    height: 400px;
  }
  
  .aboutpage {
    text-align: center;
    background-color: #346299;
    padding: 20px;
    margin-top: 20px;
  }

  .aboutpage p {
    padding: 25px;
    font-size: 18px;
  }

  .card {
    text-align: center;
    background-color: #346299;
    padding: 20px;
    margin-top: 20px;
  }

  .card p{
    padding: 25px;
    font-size: 18px;
  }
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Footer */
  .footer {
    padding: 20px;
    text-align: center;
     background-color: #CCC9E7;
    margin-top: 20px;
  }
  
  /* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other */
  @media screen and (max-width: 800px) {
    .leftcolumn, .rightcolumn {
      width: 100%;
      padding: 0;
    }
  }