.settings{
    display: flex;
}

.settingsWrapper{
    flex:9;
    padding: 20px;
}

.settingsTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settingsUpdateTitle{
    font-size: 30px;
    margin-bottom:20px ;
    color: cornflowerblue;
}

.settingsDeleteTitle{
    color: red;
    font-size: 12px;
    cursor: pointer;
}

.settingsForm{
    display: flex;
    flex-direction: column;
}

.settingsProfilePicture{
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.settingsProfilePicture > img{
    width: 70px;
    height: 70px;
    border-radius: 20px;
    object-fit: cover;
}

.settingsPPIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    color: coral;
    background-color: blue;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    cursor: pointer;
}

.settingsForm > label{
    font-size: 20px;
    margin-top: 20px;
}

.settingsForm > input{
    color: crimson;
    margin: 10px 0;
    height: 30px;
    border: none;
    border-bottom: 1px solid gray;
}

.settingsSubmit{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: aliceblue;
    background-color:black;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
}