.sidebar{
   
    margin: 10px;
    padding-bottom: 40px;
    background-color: bisque;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.sidebarItem{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarTitle{
    margin:10px ;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a7a7;
    border-bottom: 1px solid #a7a7a7;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.sidebarItem >img {
    margin-top: 5px;
    border-radius: 8px;
    padding: 5px;
    width: 350px;
}

.sidebarItem > p {
    width: 300px; 
    padding: 30px;
}

.sidebarList{
    list-style: none;
    margin-bottom: 30px;
}

.sidebarListItem{
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}

.sidebarSocial{
    margin-top: 10px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebarIcon{
    font-size: 15px;
    margin-left: 10px ;
    cursor: pointer;
}